<template>
    <el-autocomplete
        v-model="selectItem.selectName"
        :fetch-suggestions="querySearchAsync"
        @select="handleSelect"
        @blur="blurInput"
        v-bind="attributes"
    ></el-autocomplete>
</template>
<script>
import axios from 'axios'
export default {
    name: 'suggestion',
    props: {
        requestType: {
            type: Number,
            default: 0,
            desc: '建议搜索类型 0:加载全部搜索 1:模糊输入搜索'
        },
        selectItem: {
            type: Object,
            default: () => {
                return {
                    selectName: '',
                    selectCode: ''
                }
            },
            desc: '默认值或者选中的值'
        },
        searchUrl: {
            type: String,
            default: '',
            desc: '默认请求接口url'
        },
        keyword: {
            type: String,
            default: 'keyword',
            desc: '默认搜索的key'
        },
        searchParam: {
            type: Object,
            default: () => {
                return {
                }
            },
            desc: '默认搜索其他关键词'

        },
        selectKey: {
            type: Object,
            default: () => {
                return {
                    keyName: 'organizationShortName',
                    keyValue: 'organizationCode'
                }
            },
            desc: '下拉类型显示的数据'
        },
        defaultAttributes: {
            type: Object,
            default: () => {
                return {

                }
            },
            desc: 'ele 自有的属性请参考文档'
        }
    },
    data () {
        return {
            sugArray: []
        }
    },
    computed: {
        attributes () {
            const defaultAttributes = Object.assign({
                placeholder: '请输入内容',
                maxlength: 15,
                debounce: 300
                // triggerOnFocus: false
            }, this.defaultAttributes)
            return defaultAttributes
        }
    },
    async mounted () {
        if (this.requestType === 0) {
            const { data } = await axios.post(this.searchUrl, this.searchParam)
            this.selectArray = data.data && data.data.map((value) => {
                value.value = value[this.selectKey.keyName]
                value.selectCode = value[this.selectKey.keyValue]
                return value
            })
        }
    },
    methods: {
        clearSuggest () {
            this.selectObj.selectCode = ''
            this.selectObj.selectName = ''
        },
        async querySearchAsync (queryString, callback) {
            if (this.requestType === 1) {
                this.selectArray = []
            }
            this.searchParam[this.keyword] = queryString
            if (this.requestType === 1 && queryString) {
                const { data } = await axios.post(this.searchUrl, this.searchParam)
                this.selectArray = data.data && data.data.map((value) => {
                    value.value = value[this.selectKey.keyName]
                    value.selectCode = value[this.selectKey.keyValue]
                    return value
                })
            }
            // TODO 在模糊搜索时候 空值不显示 数组不存在默认给[]
            const selectList = this.selectArray ? this.selectArray : []
            this.sugArray = queryString ? selectList.filter(this.createStateFilter(queryString)) : selectList
            // 当初这边有个定时器 有个请求过渡效果
            // callback(results)
            callback(this.sugArray)
        },
        createStateFilter (queryString) {
            return (state) => {
                return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
            }
        },
        handleSelect (item) {
            this.selectItem.selectName = item.value
            this.selectItem.selectCode = item.selectCode
            this.$emit('back-event', item)
        },
        blurInput () {
            const results = this.sugArray.filter(item => {
                return (item.value === this.selectItem.selectName)
            })
            if (!(results.length > 0 && this.selectItem.selectCode)) {
                // 回选 验证 清空
                this.selectItem.selectName = ''
                this.selectItem.selectCode = ''
                this.$emit('back-event', '')
            }
        }
    }
}
</script>

